body {
  background-color: rgba(254, 242, 242, .78);
}

.search-form {
  width: 350px;
  height: 40px;
  background: #fff;
  border-radius: 40px;
  margin-top: 40px;
  transition: all .3s;
  position: relative;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 6px 8px rgba(0, 0, 0, .15);
}

input {
  color: #5a6674;
  width: 195px;
  height: 20px;
  -webkit-appearance: none;
  -ms-appearance: none;
  appearance: none;
  background: none;
  border: none;
  outline: none;
  font-size: 14px;
  position: absolute;
  top: 10px;
  left: 38px;
}

.search_btn {
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: 1px solid rgba(0, 0, 0, 0);
  position: absolute;
  top: 5px;
  right: 12px;
}

.search_btn-svg {
  fill: #d94242;
}

.search_btn-svg:hover {
  transform: scale(1.15);
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 25px;
  display: flex;
}

.photo-card {
  width: 320px;
  height: 290px;
  background-color: rgba(250, 218, 218, .76);
  border-radius: 3px;
  transition: all .2s ease-in-out;
  box-shadow: 0 2px 5px -1px rgba(57, 25, 57, .365), 0 1px 3px -1px rgba(0, 0, 0, .3);
}

.photo-card:hover, .photo-card:focus {
  transform: scale(1.1);
}

.photo-card__img {
  width: 320px;
  height: 220px;
}

.info {
  width: calc(33.3333% - 6.66667px);
  flex-basis: 100px;
  gap: 18px;
  display: flex;
}

.info p {
  color: rgba(66, 18, 18, .86);
  text-align: center;
  margin-left: 5px;
  font-size: 15px;
}

.load-more {
  cursor: pointer;
  width: 130px;
  height: 40px;
  color: #fff;
  background-color: #d94242;
  border: 1px solid rgba(0, 0, 0, 0);
  border-radius: 10px;
  margin: 20px auto;
  font-size: 16px;
}

.load-more:hover, .load-more:focus {
  color: #d94242;
  background-color: rgba(0, 0, 0, 0);
  border: 1px solid #d94242;
}

